html {
  font-family: "Gilroy", "Arial", sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;

  -webkit-tap-highlight-color: $transparent;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

img {
  max-width: 100%;
  height: auto;
}

input,
textarea,
select {
  border-radius: 0;
  box-shadow: none;
  -webkit-appearance: none;
}